"use client";

import { User, TrendingUp, Trophy, DollarSign } from "lucide-react";

interface MetricCardProps {
  title: string;
  value: string | number;
  icon: React.ReactNode;
  change: string;
  changePositive?: boolean;
  bgColor?: string;
}

function MetricCard({ title, value, icon, change, changePositive = true, bgColor = "bg-blue-100" }: MetricCardProps) {
  return (
    <div className="bg-white rounded-lg shadow p-4 flex items-center space-x-4 min-w-[180px]">
      <div className={`p-3 rounded ${bgColor} text-blue-600`}>
        {icon}
      </div>
      <div>
        <p className="text-sm text-gray-600">{title}</p>
        <p className="text-xl font-bold">{value}</p>
        <p className={`text-xs font-semibold ${changePositive ? "text-green-600" : "text-red-600"}`}>
          {change} vs last month
        </p>
      </div>
    </div>
  );
}

export default function MetricsCards() {
  return (
    <div className="flex space-x-6 mb-8">
      <MetricCard
        title="Total Leads"
        value="..."
        icon={<User size={24} />}
        change="12%"
        bgColor="bg-blue-100"
      />
      <MetricCard
        title="Active Leads"
        value="..."
        icon={<TrendingUp size={24} />}
        change="8%"
        bgColor="bg-green-100"
      />
      <MetricCard
        title="Converted"
        value="..."
        icon={<Trophy size={24} />}
        change="23%"
        bgColor="bg-yellow-100"
      />
      <MetricCard
        title="Quote Value"
        value="..."
        icon={<DollarSign size={24} />}
        change="18%"
        bgColor="bg-red-100"
      />
    </div>
  );
}
