"use client";

import { useState } from "react";
import { HomeIcon, UsersIcon, ClipboardListIcon, ChartBarIcon } from "lucide-react";
import { useUser } from "@/context/UserContext";

export default function Sidebar() {
  const [active, setActive] = useState("dashboard");
  const { role } = useUser();

  const adminMenuItems = [
    { id: "dashboard", label: "Dashboard", icon: HomeIcon },
    { id: "lead-management", label: "Lead Management", icon: UsersIcon },
    { id: "activities", label: "Activities", icon: ClipboardListIcon },
    { id: "user-management", label: "User Management", icon: UsersIcon },
    { id: "reports", label: "Reports", icon: ChartBarIcon },
  ];

  const commercialMenuItems = [
    { id: "dashboard", label: "Dashboard", icon: HomeIcon },
    { id: "lead-management", label: "Lead Management", icon: UsersIcon },
    { id: "activities", label: "Activities", icon: ClipboardListIcon },
  ];

  const menuItems = role === "admin" ? adminMenuItems : commercialMenuItems;

  return (
    <aside className="w-64 bg-white border-r border-gray-200 flex flex-col justify-between h-screen p-4">
      <div>
        <h1 className="text-xl font-bold text-blue-600 mb-8 cursor-pointer">CRM System</h1>
        <nav className="space-y-2">
          {menuItems.map((item) => {
            const Icon = item.icon;
            const isActive = active === item.id;
            return (
              <button
                key={item.id}
                onClick={() => setActive(item.id)}
                className={`flex items-center space-x-3 w-full px-3 py-2 rounded ${
                  isActive ? "bg-blue-600 text-white" : "text-gray-700 hover:bg-gray-100"
                }`}
              >
                <Icon size={20} />
                <span className="font-medium">{item.label}</span>
              </button>
            );
          })}
        </nav>
      </div>
      <div className="flex items-center space-x-3 p-3 border-t border-gray-200">
        <div className="w-10 h-10 rounded-full bg-blue-600 flex items-center justify-center text-white font-bold">
          JD
        </div>
        <div>
          <p className="text-sm font-semibold">John Doe</p>
          <p className="text-xs text-gray-500">Sales Manager</p>
        </div>
      </div>
    </aside>
  );
}
