"use client";

import { Bell, Plus } from "lucide-react";
import LeadAssignmentButton from "./LeadAssignmentButton";
import { useState, useEffect } from "react";
import { useUser } from "@/context/UserContext";

export default function Header() {
  const [notifications, setNotifications] = useState(0);
  const { role, setRole } = useUser();
  const currentUserId = 1; // Simulated current commercial user ID

  useEffect(() => {
    const interval = setInterval(async () => {
      try {
        const res = await fetch("/api/leads");
        const leads = await res.json();
        // Count leads assigned to current user that have been updated recently
        const updatedLeads = leads.filter(
          (lead: any) =>
            lead.assigned_to === currentUserId &&
            new Date(lead.updated_at) > new Date(Date.now() - 24 * 60 * 60 * 1000) // updated in last 24h
        );
        setNotifications(updatedLeads.length);
      } catch (error) {
        console.error("Error fetching leads for notifications", error);
      }
    }, 60000); // poll every 60 seconds

    return () => clearInterval(interval);
  }, []);

  return (
    <header className="flex items-center justify-between p-4 border-b border-gray-200 bg-white">
      <h2 className="text-xl font-bold">Dashboard</h2>
      <div className="flex items-center space-x-4">
        {role === "admin" && <LeadAssignmentButton />}
        <button className="relative p-2 rounded hover:bg-gray-100" title="Notificaciones">
          <Bell size={24} />
          {notifications > 0 && (
            <span className="absolute top-0 right-0 inline-flex items-center justify-center px-2 py-1 text-xs font-bold leading-none text-white bg-red-600 rounded-full">
              {notifications}
            </span>
          )}
        </button>
        {role === "admin" && (
          <select
            className="border border-gray-300 rounded p-1"
            value={role}
            onChange={(e) => setRole(e.target.value as "admin" | "commercial")}
            title="Cambiar rol de usuario"
          >
            <option value="admin">Administrador</option>
            <option value="commercial">Comercial</option>
          </select>
        )}
      </div>
    </header>
  );
}
